.radio-box {
  margin-top: 26px;
  margin-left: 203px;
}
.radio input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 7px;
}
.radio-text {
  position: relative;
  padding: 0 0 0 20px;
  cursor: pointer;
  font-family: Montserrat-Regular;
  color: black;
  font-size: 13px;

}
.radio-text:before {
  content: '';
  position: absolute;
  top: 3px;
  left: 0;
  width: 13px;
  height: 13px;
  border: 1px solid #CDD1DA;
  border-radius: 50%;
  background: #FFF;
}
.radio-text:after {
  content: '';
  position: absolute;
  top: 7px;
  left: 4px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: #904b92;
  box-shadow: inset 0 1px 1px rgba(0,0,0,.5);
  opacity: 0;
}
.radio input:checked + .radio-text:after {
  opacity: 1;
}

.checkbox {
  margin-left: 203px;
}

.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 7px 0 0 20px;
}
.checkbox-text {
  width: 230px;
  position: relative;
  padding: 0 0 0 20px;
  cursor: pointer;
  font-family: Montserrat-Regular;
  color: black;
  font-size: 13px;
}

.checkbox-text:before {
  content: '';
  position: absolute;
  top: 4px;
  left: 0;
  width: 13px;
  height: 13px;
  border-radius: 3px;
  border: 1px solid #CDD1DA;
  background: white;
}

.checkbox input:checked + .checkbox-text:before {
  background: #fff7c0 url('../../../images/checkboxMarker.svg') 2px 0 no-repeat;
}

.form-footer button {
